import React from 'react';
import "./StepsComponent.scss";

const StepsComponent = ({steps = []}) =>
    <div id="StepsComponent">

        <h1 className="text-align-center font-weight-light color-primary">How it Works</h1>

        <div className="stepsContainer">

            {steps.map((step, id) => {

                return <div className="layout">
                    <div key={id} className="stepContainer">

                        <img className="stepIcon" src={step.icon} alt={step.title}/>
                        <div>
                            <h2>{step.title}</h2>

                            <p className="stepDescription">{step.description}</p>
                        </div>
                    </div>
                </div>
            })}

        </div>

    </div>;

export default StepsComponent;